import {html, LitElement} from 'lit-element/lit-element.js';
import {unsafeHTML} from 'lit-html/directives/unsafe-html';
import observable from 'proxy-observable/src/observable';

/**
 * @property {observable} _left
 * @property {observable} _center
 * @property {observable} _right
 */
export default class StatusBar extends LitElement {

	static get properties() {
		return {
			left:   { type: Array },
			center: { type: Array },
			right:  { type: Array },
		};
	}

	get left() { return this.observe('_left', this._left); }

	set left(val) { this.setProp('left', val); }

	get center() { return this.observe('_center', this._center); }

	set center(val) { this.setProp('center', val); }

	get right() { return this.observe('_right', this._right); }

	set right(val) { this.setProp('right', val); }

	constructor() {
		super();
		this.left   = [];
		this.center = [];
		this.right  = [];
	}

	setProp(name, value) {
		if(!Array.isArray(value))
			value = [value];

		let oldVal       = this[`_${name}`];
		this[`_${name}`] = value;

		this.requestUpdate(`_${name}`, oldVal);
	}

	observe(prop, arr) {
		arr = observable(arr);
		arr.on('any', (value, oldVal, e) => {
			this.requestUpdate(prop, oldVal);
		});
		return arr;
	}

	/**
	 * Implement `render` to define a template for your element.
	 *
	 * You must provide an implementation of `render` for any element
	 * that uses LitElement as a base class.
	 */
	render() {
		/**
		 * `render` must return a lit-html `TemplateResult`.
		 *
		 * To create a `TemplateResult`, tag a JavaScript template literal
		 * with the `html` helper function:
		 */

		return html`
			<!-- template content -->
			<STYLE>
				:host { position: fixed; left: 0px; bottom: 0px; width: -moz-available; width: available; }
				DIV { padding: 0px 4px; }
				DIV#bar { display: flex; background-color: #e0e0e0; justify-content: space-between; padding: 1px 8px; height: 1.25em; }
				DIV#bar > DIV { display: flex; }
			</STYLE>
			<DIV id="bar">
				<DIV>
  					${this._left.map(i => html`<div>${this.renderItem(i)}</div>`)}
				</DIV>
				<DIV>
  					${this._center.map(i => html`<div>${this.renderItem(i)}</div>`)}
  				</DIV>
				<DIV>
  					${this._right.map(i => html`<div>${this.renderItem(i)}</div>`)}
				</DIV>
			</DIV>
		`;
	}

	/**
	 * @param {any} i
	 */
	renderItem(i) {
		if(typeof i === 'object') {
			return i;
		}
		return unsafeHTML(i);
	}


}

console.log('status-bar loaded');

// Register the new element with the browser.
customElements.define('status-bar', StatusBar);
